import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import './Useheader.css';
import Offcanvas from 'react-bootstrap/Offcanvas';
import CloseButton from 'react-bootstrap/CloseButton';
import {withRouter} from 'react-router-dom';


class Header extends Component {
    constructor(props){
        super(props);
        console.log(">>> inside nanetConstructor", props)
        this.state={
            show:false
        }
    }

    handleShow(){
        this.setState({show:true})
    }

    handleClose(){
        this.setState({show:false})
    }


    render(){
        return (
            <>
                <div className="headerbackgrd">
                    <span>
                       
                            <center>
                               
                                <Link to='/'><img src= "https://i.ibb.co/phLcJRb/Beckette-png-logo.png" style={{width:"80px", height:"60px", marginTop:"20px",marginBottom:"20px"}} alt="logo"/></Link>
                                
                                <p className="brandNme">The Beckett Suites</p>
                            </center>
                       
                        
                    </span>
                    <span>
                        
                        <i type="button" className="fa fa-bars size2" onClick={()=>this.handleShow()}></i>
                    
                    </span>
                    <span>
                        <a className="shift" href="https://emailmg.globat.com/roundcube/" target='_blank' rel="noopener noreferrer" aria-label='email'>
                            <i className="fa fa-envelope-o envelopefrmt size4" aria-hidden="true"></i>
                        </a>
                    </span>
                    <span>
                        
                        <i className="fa fa-cog fa-spin fa-1.8x fa-fw size5" type="button" onClick={()=>this.props.history.push('/Settings')}></i>
                        <span className="sr-only">Loading...</span>
                        
                    </span>
                   
                </div>
                <div className="headerbackgrd2">
                    <span>
                        <center>
                            <a className="shift" href="http://www.twitter.com" target='_blank' rel="noopener noreferrer" aria-label='twitter'>
                                <i className="fab fa-twitter sizeN"></i>
                            </a>
                            
                        </center>
                        
                        
                    </span>
                    <span>
                        <center>
                            <a className="shift" href="http:www.facebook.com" target='_blank' rel="noopener noreferrer" aria-label='twitter'>
                                <i className="fab fa-facebook sizeN"></i>
                            </a>
                            
                        </center>
                        
                        
                    </span>
                    <span>
                        <center>
                            <a className="shift" href="http://www.instagram.com" target='_blank' rel="noopener noreferrer" aria-label='twitter'>
                                <i className="fab fa-instagram sizeN"></i>
                            </a>
                            
                        </center>
                        
                    </span>
                </div>
                
                <Offcanvas show={this.state.show}>
                    
                    <div className="headerbackgrd3">
                        <Offcanvas.Body>
                            
                            <div className="bg-dark p-3 size3">
                                <CloseButton variant="white" onClick={()=>this.handleClose()}/>
                                
                            </div>
                            <div className="menu">
                                <center>
                                    <p className="menulist" type="button" onClick={()=>{this.props.history.push('/');this.handleClose()}}>Home</p>
                                </center>
                            </div>
                            <div className="menu">
                                <center>
                                    <p className="menulist" type="button" onClick={()=>{this.props.history.push('/booking');this.handleClose()}}>Rooms</p>
                                </center>
                            </div>
                            <div className="menu">
                                <center>
                                    <p className="menulist" type="button" onClick={()=>{this.props.history.push('/explore');this.handleClose()}}>Explore</p>
                                </center>
                            </div>
                            <div className="menu">
                                <center>
                                    <p className="menulist" type="button" onClick={()=>{this.props.history.push('/Events');this.handleClose()}}>Events</p>
                                </center>
                            </div>
                            <div className="menu">
                                <center>
                                    <p className="menulist" type="button" onClick={()=>{this.props.history.push('/Contact');this.handleClose()}}>Contact Us</p>
                                </center>
                            </div>
                            
                        </Offcanvas.Body>
                    </div>
                    
                </Offcanvas>
                
                
            
            </>
        )
    }
    
    
}


export default withRouter(Header);