import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import './Header.css';
import './Useheader.css'
import {withRouter} from 'react-router-dom';


const url = "https://beckettlogin.onrender.com/api/auth/userinfo";

class Header2 extends Component {
    constructor(props) {
        super()

        this.state={
            userdata:''
        }
    }
   handleLogout = () => {
        this.setState({userdata:''})
        localStorage.removeItem('userdata')
        localStorage.removeItem('rtk')
        this.props.history.push('/Admin')
    }

    condition1Header = () => {
        if(this.state.userdata.name){
            let data = this.state.userdata;
            let outputArray = [data.name,data.email,data.phone,data.role]
            localStorage.setItem('userdata',outputArray)
            return(
                <>
                    
                    <button type="button" className="btn btn-outline-* color">
                        <i className="fas fa-user-check color"></i> &nbsp;
                        <b>Hi, {outputArray[0]}</b>
                    </button>
                    <button type="button" className="logout btn btn-outline-danger" onClick={this.handleLogout}>
                        Logout
                    </button>
                    
                </>
            )
        }

        else{
            return(
                <>
                    <Link to="/Admin" className="buttoncntrl1 btn btn-outline-success ">Login</Link>
                    
                </>
            )
        }
    }

    render(){
        return (
            <>
                {/* <nav className="navbar navbar-expand-lg navbar-light bg-dark">
                    <div className="container-fluid ">
                        <div className="navbar-brand">
                            <Link to="/Settings">
                                <center><img src= "https://i.ibb.co/phLcJRb/Beckette-png-logo.png" style={{width:"80px", height:"60px"}} alt="logo"/></center>
                                
                            </Link>
                            <p className="brandNme">The Beckett Suites</p>
                        </div>
                       
                        <div className="detext">
                           {this.condition1Header()}
                        </div>
                        
                    </div>
                </nav> */}
                <div className="headerbackgrd">
                    <span>
                       
                            <center>
                               
                                <Link to='/'><img src= "https://i.ibb.co/phLcJRb/Beckette-png-logo.png" style={{width:"80px", height:"60px", marginTop:"20px",marginBottom:"20px"}} alt="logo"/></Link>
                                
                                <p className="brandNme">The Beckett Suites</p>
                            </center>
                       
                        
                    </span>
                    <span>
                        
                        <i type="button" className="fa fa-bars size2" onClick={()=>this.handleShow()}></i>
                    
                    </span>
                    <span>
                        <a className="shift" href="https://emailmg.globat.com/roundcube/" target='_blank' rel="noopener noreferrer" aria-label='email'>
                            <i className="fa fa-envelope-o envelopefrmt size4" aria-hidden="true"></i>
                        </a>
                    </span>
                    <span>
                        
                        <i className="fa fa-cog fa-spin fa-1.8x fa-fw size5" type="button" onClick={()=>this.props.history.push('/Settings')}></i>
                        <span className="sr-only">Loading...</span>
                        
                    </span>
                   
                </div>
                <div className="headerbackgrd2">
                    <span>
                        <center>
                            <a className="shift" href="http://www.twitter.com" target='_blank' rel="noopener noreferrer" aria-label='twitter'>
                                <i className="fab fa-twitter size"></i>
                            </a>
                            
                        </center>
                        
                        
                    </span>
                    <span>
                        <center>
                            <a className="shift" href="http:www.facebook.com" target='_blank' rel="noopener noreferrer" aria-label='twitter'>
                                <i className="fab fa-facebook size"></i>
                            </a>
                            
                        </center>
                        
                        
                    </span>
                    <span>
                        <center>
                            <a className="shift" href="http://www.instagram.com" target='_blank' rel="noopener noreferrer" aria-label='twitter'>
                                <i className="fab fa-instagram size"></i>
                            </a>
                            
                        </center>
                        
                    </span>
                    <div className="detext">
                        {this.condition1Header()}
                    </div>
                </div>
            
            </>
        )
    }
    componentDidMount(){
        fetch(url, {
            method: 'GET',
            headers:{
                'x-access-token':localStorage.getItem('rtk')
            }
        })
        .then((res) => res.json())
        .then((data) => {
            this.setState({
                userdata:data
            })
        })
    }

    
}
export default withRouter (Header2);